function getEducations() {


    listContainer.innerHTML = "";
    
    listContainer.innerHTML +=
    `<div class="rowWrapper">
        <div class="col1">
            <b>University:</b>
        </div>
        <div class="col2">
            <b>Utbildning:</b> 
        </div>
        <div class="col3">
            <b>Start datum:</b>
        </div>
        <div class="col4">
            <b>Slut datum:</b>
        </div>
    </div>
    `;

    // local url http://localhost/moment5ws/api
    fetch('http://10.0.1.222/projekt_wiii_ws/api?type=edu')
    .then(response => response.json())
    .then(data => {
        data.forEach(edu => {
            console.log(edu.university);
            listContainer.innerHTML +=
            `<div class="rowWrapper">
                <div class="col1">
                    ${edu.university}
                </div>
                <div class="col2">
                    ${edu.course}
                </div>
                <div class="col3">
                    ${edu.startDate}
                </div>
                <div class="col4">
                    ${edu.endDate}
                </div>
                <div class="col5">
                    <button id="${edu.id}" class="btn_del" onClick="deleteEdu(${edu.id})">Radera</button><br>
                    <button id="${edu.id}" class="btn_update" onClick="setFormEdu(${edu.id})">Ändra</button>
                </div>
            </div>`;
            
        });
    })

}